import React, {Component} from 'react'
import Score from './score'
import {askAQuestion, isItCorrect, ASKED_QUESTIONS, SCORE, NEW_QUESTIONS} from './questions'
import { Button, Grid } from 'semantic-ui-react'

export default class Quiz extends Component {
    constructor(props) {
        super(props)
        this.state = {
            correct : false,
            question : askAQuestion(),
            show : false,
            gameOver : false
        }
        this.checkAnswer = this.checkAnswer.bind(this)
    }

    checkAnswer(e,o) {
        e.preventDefault()
        const correct = isItCorrect(e.target.dataset.index)
        this.setState({correct : correct, show : true})
        setTimeout(() => {
            const gameOver = NEW_QUESTIONS.length === 0
            const question = gameOver ? '' : askAQuestion()
            this.setState({
                correct : false,
                question : question,
                show : false,
                gameOver : gameOver
            })
        }, 1500)
    }

    render() {
        const { question, correct, show, gameOver } = this.state
        return (
            <Grid.Column width={14} center>
            {
                !gameOver ? (
                    <div>
                        <h4>{question.question}</h4>
                        {
                            question.answers.map((answer, index) => {
                                return(
                                    <Button style={{marginTop : "4px"}} basic fluid key={index} data-index={index} onClick={this.checkAnswer}>{answer.text}</Button>
                                )
                            })
                        }
                        {
                            show ? (
                                <Score
                                    icon={correct ? 'checkmark' : 'remove'}
                                    score={SCORE}
                                    questions={ASKED_QUESTIONS.length}
                                />
                            ) : (
                                null
                            )
                        }
                    </div>

                ) : (
                    <Score
                        score={SCORE}
                        questions={ASKED_QUESTIONS.length}
                        gameOver={gameOver}
                    />
                )
            }
            </Grid.Column>
        )
    }
}


