import React, {Component} from 'react'
import { Message } from 'semantic-ui-react'

export default function Score (props) {
    if (props.gameOver) {
        return(
            <Message
                icon='smile'
                header={`your final score is ${props.score} out of ${props.questions}`}
                content='Refresh this page to play again'
            />        
        )
    }
    return(
        <Message
            icon={props.icon}
            header={`your score is ${props.score} out of ${props.questions}`}
            // content='Get the best news in your e-mail every day.'
        />        
    )
}


